.title_portfolio{text-align:center;margin-bottom:30px;z-index:7;display:inline-block;font-size:3.5rem;padding:15px 30px;border-radius:10px;font-weight:900;letter-spacing:4px;border:3px solid #d4af37;box-shadow:0 6px 20px rgba(0,0,0,.3),0 0 30px rgba(212,175,55,.5),inset 0 2px 10px rgba(244,208,63,.3);position:relative;overflow:hidden}.title_portfolio::before{content:"ð“‚€";position:absolute;left:10px;top:50%;transform:translateY(-50%);font-size:1.5rem;opacity:.6}.title_portfolio::after{content:"ð“‚€";position:absolute;right:10px;top:50%;transform:translateY(-50%);font-size:1.5rem;opacity:.6}.conteneur-titre{text-align:center}.title_char{display:inline-block;animation:jump .5s ease-in-out infinite}.conteneur-portfolio{border-radius:32px;padding:5%;margin-bottom:80px;box-sizing:border-box;width:90%;position:relative;background:rgba(26,26,46,.4);-webkit-backdrop-filter:blur(40px) saturate(180%);backdrop-filter:blur(40px) saturate(180%);border:1px solid rgba(201,168,88,.3);box-shadow:0 16px 64px rgba(0,0,0,.4),0 0 100px rgba(201,168,88,.2),inset 0 1px 0 hsla(0,0%,100%,.1),inset 0 -1px 0 rgba(201,168,88,.2)}.conteneur-portfolio::before{content:"";position:absolute;inset:-2px;border-radius:32px;padding:2px;background:linear-gradient(135deg, rgba(201, 168, 88, 0.4), rgba(232, 201, 137, 0.2), rgba(201, 168, 88, 0.4));-webkit-mask:linear-gradient(#fff 0 0) content-box,linear-gradient(#fff 0 0);-webkit-mask-composite:xor;mask-composite:exclude;opacity:.6;animation:borderGlow 4s ease-in-out infinite}.conteneur-portfolio::after{content:"";position:absolute;inset:0;border-radius:32px;background:radial-gradient(circle at 50% 0%, rgba(201, 168, 88, 0.1), transparent 60%);pointer-events:none}@keyframes borderGlow{0%,100%{opacity:.4}50%{opacity:.8}}.filtres{display:flex;justify-content:center;margin-bottom:20px}.filtres button{margin:0 10px;padding:12px 28px;border:none;border-radius:12px;cursor:pointer;transition:var(--transition);font-size:1.1rem;margin-bottom:30px;font-weight:600;letter-spacing:1.5px;position:relative;overflow:hidden;background:rgba(26,26,46,.6);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);color:#f5e8d3;box-shadow:0 4px 16px rgba(0,0,0,.3),inset 0 1px 0 hsla(0,0%,100%,.1),0 0 0 1px rgba(201,168,88,.2)}.filtres button::before{content:"";position:absolute;inset:0;background:linear-gradient(135deg, rgba(201, 168, 88, 0.2), rgba(232, 201, 137, 0.3), rgba(201, 168, 88, 0.2));opacity:0;transition:opacity .4s ease}.filtres button::after{content:"";position:absolute;top:50%;left:50%;width:0;height:0;border-radius:50%;background:radial-gradient(circle, rgba(232, 201, 137, 0.4), transparent);transform:translate(-50%, -50%);transition:width .6s ease,height .6s ease}.filtres button:active{transform:translateY(-2px)}.filtres button.active{background:linear-gradient(135deg, #c9a858, #e8c989);color:#1a1a2e;font-weight:700;box-shadow:0 6px 20px rgba(201,168,88,.5),inset 0 1px 0 hsla(0,0%,100%,.3),0 0 0 1px rgba(201,168,88,.6)}.grid_projets{display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));gap:32px;margin-left:0px;padding:20px 0}@media(min-width: 768px)and (max-width: 1023.9px){.title_portfolio{font-size:2.5rem}.grid_projets{grid-template-columns:repeat(3, 1fr);margin-left:3%}.filtres button{font-size:1.1rem}.conteneur-portfolio{background-size:contain}}@media(min-width: 320px)and (max-width: 767.9px){.title_portfolio{font-size:1.5rem;margin-top:15px;margin-bottom:0px;padding:10px 15px;letter-spacing:2px}.grid_projets{display:flex;flex-direction:column;align-items:center}.filtres{margin-top:20px;display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr);justify-items:center;align-items:center}.filtres button{padding:5px 10px;font-size:.9rem;margin:5px}.filtres button:last-child{grid-column:span 2;justify-self:center;height:40px;width:120px}.conteneur-portfolio{background-size:contain}}